import Container from './Container';
import { IconContext } from 'react-icons';
import { IoMdNotificationsOutline, IoMdCart } from 'react-icons/io';
import '../css/Header.css';
import { Link, useNavigate } from 'react-router-dom';
import React from 'react';
import { getToken } from '../services/Auth';
import { Apiauthenticated } from '../services/api';
import { removeToken } from '../services/Auth';

const Header = (props) => {
  const navigate = useNavigate();
  const role = getToken();
  const [cartItems, SetCartItems] = React.useState(0);
  const [isLogged, setIsLooged] = React.useState(false);
  React.useEffect(() => {
    const fetch = (async () => {
      try {
        const response = (await Apiauthenticated.get('open_cart')).data;
        if ((await Apiauthenticated.get('open_cart'))['status'] == 200) {
          setIsLooged(true)
        } else {
          setIsLooged(false)
        }
        SetCartItems(response['sale_items'].length)
      } catch {
        const getUserData = getToken();
        console.log(getUserData)
      }
    });
    fetch();
  }, [])

  function signOut() {
    removeToken();
    navigate('/');
    window.location.reload();
  }

  return (
    <header>
      <Container>
        <Link to='/'>
          <div className='container-logo'>
            <img src='http://localhost:8080/uploads/logo.png' />
          </div>
        </Link>
        <div className='container-links'>
          <Link to='/cart'>
            <IconContext.Provider value={{ size: '24px', color: 'white' }}>
              <div style={{ display: 'flex' }}>
                <IoMdCart />
                <span className="notification-badge" style={{ color: 'red' }}>{cartItems}</span>
              </div>
            </IconContext.Provider>
          </Link>
          {role['role'] === 'admin' ? (
            <Link style={{ marginLeft: "30px" }}  to="/criar_produto">
              Criar Produto
            </Link>
          ) : null}
          {role['role'] === 'admin' ? (
            <Link style={{ marginLeft: "30px" }}  to="/tipo_produto">
              Cadastrar taxa
            </Link>
          ) : null}
          {isLogged === false ? (
            <Link style={{ marginLeft: "30px" }} to="/login">
              Login
            </Link>
          ) : null}
          {isLogged === true ? (
            <button style={{ marginLeft: "30px" }} onClick={signOut}>
              Sair
            </button>
          ) : null}
        </div>
      </Container>
    </header>
  );
};

export default Header;
