import React from 'react';
import '../css/CartItem.css';
import Card from 'react-bootstrap/Card';
import ListGroup from 'react-bootstrap/ListGroup';
import { Apiauthenticated } from '../services/api';
import toastr from 'toastr'
import 'toastr/build/toastr.min.css'
import { useNavigate } from 'react-router-dom';

const CartItem = (props) => {
  const navigate = useNavigate();
  toastr.options = {
    positionClass : 'toast-top-right',
    hideDuration: 300,
    timeOut: 60000
  }
  toastr.clear()
  const { cartData } = props;


  async function checkoutCart(){
    try{
      await Apiauthenticated.put('checkout');
      setTimeout(() => toastr.success(`Compra finalizada`), 300)
      setTimeout(()=>{
        navigate('/');
        window.location.reload();
      },3000)
    }catch(e){
      console.log(e)
    }
  }

  
  return (
    <Card style={{width:'70%'}} className="shopping-cart">
      <Card.Header className="shopping-cart-header">Shopping Cart</Card.Header>
      <Card.Body>
        <div className="total-values">
          <strong>Valor Total: </strong> R$ {cartData['total_value']}
        </div>
        <div className="total-values">
          <strong>Imposto Total</strong> R$ {cartData['total_tax']}
        </div>
        <hr />
        <h5 className="items-title">Items:</h5>
        <ListGroup className="items-list">
          {cartData['sale_items'].map((item) => (
            <ListGroup.Item key={item.id} className="item">
              <div>
                <strong>Produto: </strong> {item['product'].name}
              </div>
              <div>
                <strong>Quantidade:</strong> {item.quantity}
              </div>
              <div>
                <strong>Valor unitário:</strong> R${item['product'].price}
              </div>
              <div>
                <strong>Valor total do produto:</strong> R${item.item_total_value}
              </div>
              <div>
                <strong>Imposto total do produto:</strong> R${item.tax_amount}
              </div>
            </ListGroup.Item>
          ))}
        </ListGroup>
      </Card.Body>
      <button onClick={checkoutCart} style={{
        marginLeft:'auto', display:'block'
      }}>Finalizar Compra</button>
    </Card>
  );
};

export default CartItem;
