import React, { useState } from 'react';
import Container from './Container';
import '../css/ProductsHomeSection.css';
import { Link } from 'react-router-dom';
import Button from 'react-bootstrap/Button';
import Card from 'react-bootstrap/Card';
import { ApiImage, Apiauthenticated } from '../services/api';
import toastr from 'toastr'
import 'toastr/build/toastr.min.css'

const ProductsHomeSection = (props) => {
  toastr.options = {
    positionClass : 'toast-top-right',
    hideDuration: 300,
    timeOut: 60000
  }
  toastr.clear()
  const { data } = props;
  const [quantity, setQuantity] = useState(1);

  async function insertCart(){
    data['quantity'] = quantity
    try{
      Apiauthenticated.post('add_cart', data);
      setTimeout(() => toastr.success(`Produto adicionado ao carrinho`), 300)
      setTimeout(()=>{
        window.location.reload();
      },1000)
    }catch(e){
      console.log(e);
    }
  }

  return (
    <Card style={{ width: '200px', margin: '15px 10px' }}>
      <Card.Img variant="top" src={ApiImage + data['image_path']} />
      <Card.Body style={{ display:'flex', flexDirection:'column', justifyContent:'space-around'}}>
        <Card.Title>{data['name']}</Card.Title>
        <Card.Text>Preço: R$ {data['price']}</Card.Text>
        <input style={{margin:'5px 0'}} type="number" min={1} value={quantity} onChange={(e) => setQuantity(e.target.value)} />
        {quantity > 1 ? <Card.Text>SubTotal: R$ {(quantity * parseFloat(data['price'])).toFixed(2)}</Card.Text> : <Card.Text><br/></Card.Text>}
        <Button style={{marginTop:'10px'}} variant="primary" onClick={insertCart}>Adicionar ao carrinho</Button>
      </Card.Body>
    </Card>
  );
};

export default ProductsHomeSection;
