import React, { useState } from 'react';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import { setToken, getToken } from '../services/Auth';
import {Apiauthenticated, Api} from '../services/api';
import '../css/Login.css';
import { useNavigate } from 'react-router-dom';
import toastr from 'toastr'
import 'toastr/build/toastr.min.css'

function Product() {
  toastr.options = {
    positionClass : 'toast-top-right',
    hideDuration: 300,
    timeOut: 60000
  }
  toastr.clear()

  const navigate = useNavigate();

  const [formData, setFormData] = useState({
    tax: '',
    name: ''
  });

  const handleInputChange = (event) => {
    const { name, value, type, checked } = event.target;
    const inputValue = type === 'checkbox' ? checked : value;

    setFormData((prevState) => ({
      ...prevState,
      [name]: inputValue
    }));
  };

  const handleSubmit = async (event) => {
    event.preventDefault();

    try {
      const resp = (await Apiauthenticated.post('product_type',{name:formData['name']})).data;
      console.log(resp['data'])
      await Apiauthenticated.post('product_tax',{
        tax_percentage:formData['tax'],
        type_product_id:resp['data']['id']
      }
      );
      setTimeout(() => toastr.success(`Criado com sucesso!`), 300)
      setTimeout(()=>{
        navigate('/login');
        window.location.reload();
      },3000)
      
    } catch (error) {
      setTimeout(() => toastr.error(`Não foi possivel Criar, tente novamente.`), 300)
    }
  };

  return (
    <div className="form-container">
      <h1 style={{textAlign:'center'}}>Cadastre seu tipo de produto e sua taxa</h1>
      <Form onSubmit={handleSubmit}>
        
      <Form.Group className="mb-3" controlId="formBasicEmail">
          <Form.Label>Tipo</Form.Label>
          <Form.Control
            required
            type="text"
            placeholder="tipo de produto"
            name="name"
            value={formData.name}
            onChange={handleInputChange}
          />
          <Form.Text className="text-muted">
          </Form.Text>
        </Form.Group>

        <Form.Group className="mb-3" controlId="formBasicPassword">
          <Form.Label>Taxa</Form.Label>
          <Form.Control
            required
            type="number"
            placeholder="digite a taxa do produto"
            name="tax"
            value={formData.tax}
            onChange={handleInputChange}
          />
        </Form.Group>

        <Button variant="primary" type="submit">
          Cadastrar
        </Button>

      </Form>
    </div>
  );
}

export default Product;
