import React, { useState } from 'react';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import { setToken, getToken } from '../services/Auth';
import {Apiauthenticated, Api} from '../services/api';
import '../css/Login.css';
import { useNavigate } from 'react-router-dom';
import toastr from 'toastr'
import 'toastr/build/toastr.min.css'

function Login() {
  toastr.options = {
    positionClass : 'toast-top-right',
    hideDuration: 300,
    timeOut: 60000
  }
  toastr.clear()

  const navigate = useNavigate();
  React.useEffect(()=>{
    console.log('s')
     const fetch = (async ()=>{
      try{
        await Apiauthenticated.get('open_cart');
        setTimeout(() => toastr.info(`Você já está logado.`), 300)
        navigate('/')
      }catch(e){
        console.log(e);
      }
     });
     fetch();
  },[])

  const [formData, setFormData] = useState({
    email: '',
    password: ''
  });

  const handleInputChange = (event) => {
    const { name, value, type, checked } = event.target;
    const inputValue = type === 'checkbox' ? checked : value;

    setFormData((prevState) => ({
      ...prevState,
      [name]: inputValue
    }));
  };

  const handleSubmit = async (event) => {
    event.preventDefault();

    try {
      const response = await Api.post('login', formData);
      const token = response.data;
      console.log(token)
      setToken(token);
      setTimeout(() => toastr.success(`Logado com sucesso!`), 300)
      setTimeout(()=>{
        navigate('/');
        window.location.reload();
      },3000)
      
    } catch (error) {
      setTimeout(() => toastr.error(`Não foi possivel logar, tente novamente.`), 300)
      console.log(error);
    }
  };

  return (
    <div className="form-container">
      <h1 style={{textAlign:'center'}}>Login</h1>
      <Form onSubmit={handleSubmit}>
        
      <Form.Group className="mb-3" controlId="formBasicEmail">
          <Form.Label>Endereço de email</Form.Label>
          <Form.Control
            required
            type="email"
            placeholder="exemplo@exemplo.com"
            name="email"
            value={formData.email}
            onChange={handleInputChange}
          />
          <Form.Text className="text-muted">
          </Form.Text>
        </Form.Group>

        <Form.Group className="mb-3" controlId="formBasicPassword">
          <Form.Label>Senha</Form.Label>
          <Form.Control
            required
            type="password"
            placeholder="senha"
            name="password"
            value={formData.password}
            onChange={handleInputChange}
          />
        </Form.Group>

        <Button variant="primary" type="submit">
          Enviar
        </Button>

        <a className='button-sign' onClick={()=>{navigate('/register')}}>Não possui conta? <br/> Cadastre-se</a>
      </Form>
    </div>
  );
}

export default Login;
