import '../css/Cart.css';
import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import {Apiauthenticated} from '../services/api';
import CartItem from '../components/CartItem';

function Cart() {
  const [apiData, setApiData] = useState([]);
  const [loading, setLoading] = useState(true);
  const navigate = useNavigate();
  useEffect(() => {

    const result = (async () => {
      try {
        const response = (await Apiauthenticated.get('open_cart')).data;
        setApiData(response);
        setTimeout(()=>{
          setLoading(false);
        },500)
      } catch(e) {
        console.log(e);
        setLoading(true);
        setTimeout(() => {
          setLoading(false);
          navigate('/login');
          window.location.reload();
        }, 3000)
      }
    })
    result()
  }, [])

  if (loading) {
    return <div><h2>Carregando...</h2></div>;
  }

  return (
      <CartItem cartData={apiData}/>
  );
};

export default Cart;
