import React, { useState } from 'react';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import { setToken, getToken } from '../services/Auth';
import { Apiauthenticated, Api } from '../services/api';
import '../css/CreateProduct.css';
import { Navigate, useNavigate } from 'react-router-dom';
import toastr from 'toastr'
import 'toastr/build/toastr.min.css'

function CreateProduct() {
    const navigate = useNavigate();
    const [dataOption, setDataOption] = useState([]);
    React.useEffect(() => {
        async function fetch(){
            try {
                const resp =( await Apiauthenticated.get('product_type')).data;
                console.log(resp);
                setDataOption(resp)
            } catch (e) {
                console.log(e);
            }
        }fetch();
    }, [])
    toastr.options = {
        positionClass: 'toast-top-right',
        hideDuration: 300,
        timeOut: 60000,
    };
    toastr.clear();

    const [formData, setFormData] = useState({
        name: '',
        price: '',
        image: null,
        type_product_id: '',
    });

    const handleInputChange = (event) => {
        const { name, value } = event.target;
    
        setFormData((prevState) => ({
            ...prevState,
            [name]: name === 'type_product_id' ? Number(value) : value,
        }));
    };

    const handleFileChange = (event) => {
        const file = event.target.files[0];

        setFormData((prevState) => ({
            ...prevState,
            image: file,
        }));
    };

    const handleSubmit = async (event) => {
        event.preventDefault();
    
        try {
            const config = {
                headers: {
                  'Content-Type': 'multipart/form-data',
                }
              };

            const formDataWithFile = new FormData();
            formDataWithFile.append('name', formData.name);
            formDataWithFile.append('price', formData.price);
    
            if (formData.image) {
                formDataWithFile.append('image', formData.image);
            }
    
            formDataWithFile.append('type_product_id', formData.type_product_id);
    
            console.log(...formDataWithFile);
    
            const resp = await Apiauthenticated.post('product',formDataWithFile, config);
            console.log(resp.data);
    
            setTimeout(() => toastr.success(`Criado com sucesso!`), 300);
            setTimeout(() => navigate('/'), 3000);
        } catch (error) {
            setTimeout(() => toastr.error(`Não foi possível criar, tente novamente.`), 300);
            console.log(error);
        }
    };

    return (
        <div className="form-container">
            <h1 style={{ textAlign: 'center' }}>Cadastre seu produto</h1>
            <Form onSubmit={handleSubmit}>
                <Form.Group className="mb-3" controlId="name">
                    <Form.Label>Nome</Form.Label>
                    <Form.Control
                        required
                        type="text"
                        placeholder="Digite o nome do produto"
                        name="name"
                        value={formData.name}
                        onChange={handleInputChange}
                    />
                </Form.Group>

                <Form.Group className="mb-3" controlId="price">
                    <Form.Label>Preço</Form.Label>
                    <Form.Control
                        required
                        type="number"
                        placeholder="Digite o preço do produto"
                        name="price"
                        value={formData.price}
                        onChange={handleInputChange}
                    />
                </Form.Group>

                <Form.Group className="mb-3" controlId="image">
                    <Form.Label>Imagem</Form.Label>
                    <Form.Control
                        required
                        type="file"
                        accept="image/*"
                        name="image"
                        onChange={handleFileChange}
                    />
                </Form.Group>

                <Form.Group className="mb-3" controlId="type_product_id">
                    <Form.Label>Tipo de Produto</Form.Label>
                    <Form.Select
                        required
                        name="type_product_id"
                        value={formData.type_product_id}
                        onChange={handleInputChange}
                    >
                        return <option value=''></option>
                        {dataOption.map((element)=>{
                            return <option value={element['id']}>{element['name']}</option>
                        })}
                    </Form.Select>
                </Form.Group>

                <Button variant="primary" type="submit">
                    Cadastrar
                </Button>
            </Form>
        </div>
    );
}

export default CreateProduct;
