import React, { useState } from 'react';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import { setToken, getToken } from '../services/Auth';
import {Apiauthenticated, Api} from '../services/api';
import '../css/Login.css';
import { useNavigate } from 'react-router-dom';
import toastr from 'toastr'
import 'toastr/build/toastr.min.css'

function Register() {
  toastr.options = {
    positionClass : 'toast-top-right',
    hideDuration: 300,
    timeOut: 60000
  }
  toastr.clear()

  const navigate = useNavigate();

  const [formData, setFormData] = useState({
    email: '',
    password: '',
    name: ''
  });

  const handleInputChange = (event) => {
    const { name, value, type, checked } = event.target;
    const inputValue = type === 'checkbox' ? checked : value;

    setFormData((prevState) => ({
      ...prevState,
      [name]: inputValue
    }));
  };

  const handleSubmit = async (event) => {
    event.preventDefault();

    try {
      await Api.post('register', formData);
      setTimeout(() => toastr.success(`Criado com sucesso!`), 300)
      setTimeout(()=>{
        navigate('/login');
        window.location.reload();
      },3000)
      
    } catch (error) {
      setTimeout(() => toastr.error(`Não foi possivel logar, tente novamente.`), 300)
      console.log(error);
    }
  };

  return (
    <div className="form-container">
      <h1 style={{textAlign:'center'}}>Registre-se</h1>
      <Form onSubmit={handleSubmit}>
        
      <Form.Group className="mb-3" controlId="formBasicEmail">
          <Form.Label>Nome</Form.Label>
          <Form.Control
            required
            type="text"
            placeholder="nome completo"
            name="name"
            value={formData.name}
            onChange={handleInputChange}
          />
          <Form.Text className="text-muted">
          </Form.Text>
        </Form.Group>

        <Form.Group className="mb-3" controlId="formBasicEmail">
          <Form.Label>Endereço de email</Form.Label>
          <Form.Control
            required
            type="email"
            placeholder="exemplo@exemplo.com"
            name="email"
            value={formData.email}
            onChange={handleInputChange}
          />
          <Form.Text className="text-muted">
          </Form.Text>
        </Form.Group>

        <Form.Group className="mb-3" controlId="formBasicPassword">
          <Form.Label>Senha</Form.Label>
          <Form.Control
            required
            type="password"
            placeholder="senha"
            name="password"
            value={formData.password}
            onChange={handleInputChange}
          />
        </Form.Group>

        <Button variant="primary" type="submit">
          Registrar
        </Button>

      </Form>
    </div>
  );
}

export default Register;
